<template>
  
  <section>
                <figure>
                   <img class="foto" src="../assets/max.jpg">
                    <!-- figcaption -->
                    
                    <img class="snor" src="../assets/Mustache.png">
               </figure>
               
                
                <article>
                    <h2 id="home">Home</h2>
                    <!-- tekst -->
                        <p class="p-autotype">
                            Mijn naam is Max Staals 
                            <span class="typed-text">{{ typeValue }}</span> 
                            <span class="cursor" :class="{'typing' : typeStatus}">&nbsp;</span>

                        </p>
                        <p>Op het <a style="color: blue;" href="https://www.eldecollege.nl/" target="_blank">Elde College</a> in Schijndel heb ik op het VMBO de theoretische leerweg gevolgd. 
                            In 4 jaar tijd heb ik daar mijn diploma voor behaald. 
                            Nu volg ik de opleiding tot software developer op het <a style="color: blue;" href="https://www.kw1c.nl/" target="_blank">Koning Willem I College</a> in ’s-Hertogenbosch. 
                            Ik heb gekozen voor deze opleiding omdat ik het interessant vind hoe websites en applicaties in elkaar zitten. 
                        </p>
                        <p>
                            Ik werk graag in een team. Je leert dan andere mensen kennen en in een team kan iedereen van elkaars ideeën en feedback veel leren. 
                            Daarnaast kan ik ook goed zelfstandig werken.  
                        </p>
                </article>
                <div id="blue"></div>
            </section>
    
    
  
</template>

<style scoped src="../styles/Home.css">

</style>

<script>
export default {
    data: () => {
        return{
            typeValue: '',
            typeStatus: false,
            typeArray: ['en welkom op mijn website', 'en ik ben junior front-end developer', 'en ik ben junior back-end developer'],
            typingSpeed: 200,
            erasingSpeed: 100,
            newTextDelay: 200,
            typeArrayIndex: 0,
            charIndex: 0
        }
    },
    methods: {
        typeText() {
            if(this.charIndex < this.typeArray[this.typeArrayIndex].length){
                if(!this.typeStatus)
                this.typeStatus = true;

                this.typeValue += this.typeArray[this.typeArrayIndex].charAt(this.charIndex);
                this.charIndex += 1;

                setTimeout(this.typeText, this.typingSpeed);
            }
            else {
                this.typeStatus = false;
                setTimeout(this.eraseText, this.newTextDelay);
            }
        },
        eraseText() {
            if(this.charIndex > 0) {
                if(!this.typeStatus)
                this.typeStatus = true;

                this.typeValue = this.typeArray[this.typeArrayIndex].substring(0, this.charIndex - 1);
                this.charIndex -= 1;
                setTimeout(this.eraseText, this.erasingSpeed);
            }
            else {
                this.typeStatus = false;
                this.typeArrayIndex += 1;
                if(this.typeArrayIndex >= this.typeArray.length)
                    this.typeArrayIndex = 0;
                setTimeout(this.typeText, this.typingSpeed + 1000);
            }
        }
    },
    created() {
        setTimeout(this.typeText, this.newTextDelay + 200);
    }
}
    





</script>

